        left: 60px
}
}
@media (min-width: 769px){
.p_pLink{
        display: none;
}
}
@media (max-width: 768px){
html, body{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        overflow: scroll;
        /* justify-content: center;
        align-items: center; */
}
.nav_Main.right {
        right: 20px;
}
.main_Block{
        left: 0;
        top: 35px;
        width: 100vw;
}
#contact .title{
        margin-left: 0;
        margin-right: 0;
}
.content_Box{
        width: 100%;
}
#contact form{
        margin: 70px auto;
        padding: 50px 0;
}
.form_Item{
        display: block;
}
input, textarea{
        margin-top: 15px;
        padding-right: 0;
}
.p_Box{
        display: block;
        height: 450px;
}
.p_Img{
        height: 350px;
}
.p_Link{
        display: none;
}
.p_pLink{
        cursor: pointer;
}
.m_Title{
        margin: 15px 0;
}
#leftbar{
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        left: -150px;
        top: 35px;
        -webkit-transition: 200ms ease-in;
        transition: 200ms ease-in;
}
#topbar{
        display: block;
}
#footer,
    p.footer{
        display: none;
}
}
