/* input */
/* input file */
/* 選擇檔案按鈕 */
input[type=file] {
    position: absolute;
    left: -9999px;
}
label[name=file_Img]{
    background-color: #4F6463;
    color: #fff;
    font-size: 14px;
    padding: 10px;
    border-radius: 5px;
    text-align: center;
}
.form_Item.file{
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.form_Item.file img{

    width: 50px;
    height: auto;
    -webkit-clip-path: circle(35% at 50% 50%);
    clip-path: circle(50% at 50% 50%);
}

